// src/components/InstructorsPage.jsx (NY FIL)
import React, { useState } from 'react';
import Instructors from './Instructors'; // Importera huvudinstruktrssektionen
import HelpInstructors from './HelpInstructors'; // Importera hjlpinstruktrssektionen
import InstructorModal from './InstructorModal'; // Importera modalen
import '../css/instructors.css'; // Antar att din CSS fr instruktrer ocks ska glla hr


export default function InstructorsPage() {
    const [selectedInstructor, setSelectedInstructor] = useState(null);

    // Denna openModal-funktion skickas ner till bda sektionerna
    const openModal = (instructor) => setSelectedInstructor(instructor);
    const closeModal = () => setSelectedInstructor(null);



    return (
        <main className="instructors-page-content"> {/* Du kan lgga till en container fr styling */}
            {/* Huvudinstruktrssektionen */}
            <Instructors openModal={openModal} />

            {/* Hjlpinstruktrssektionen */}
            <HelpInstructors openModal={openModal} />

            {/* Modalen hanteras hr, p samma niv som sektionerna */}
            <InstructorModal
                isOpen={!!selectedInstructor}
                onClose={closeModal}
                instructor={selectedInstructor}
            />
        </main>
    );
}